import { useState } from "react";

const FileUpload = () => {
  const [file, setFile] = useState(null);

  const handleFileChange = (event) => {
    const selectedFile = event.target.files[0];
    if (selectedFile && selectedFile.type === "application/pdf") {
      setFile(selectedFile);
    } else {
      alert("Por favor, sube un archivo PDF.");
    }
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    if (file) {
      console.log("Archivo listo para subir:", file);
      // Aqu√≠ puedes enviar el archivo a tu backend
    } else {
      alert("Selecciona un archivo PDF antes de enviar.");
    }
  };

  return (
    <form onSubmit={handleSubmit} className="p-4 bg-gray-100 rounded-lg shadow-md">
      <label className="block text-lg font-semibold mb-2">Subir PDF</label>
      <input
        type="file"
        accept="application/pdf"
        onChange={handleFileChange}
        className="block w-full p-2 border border-gray-300 rounded-md"
      />
      <button
        type="submit"
        className="mt-4 px-4 py-2 bg-blue-600 text-white rounded-md hover:bg-blue-700 transition"
      >
        Subir
      </button>
    </form>
  );
};

export default FileUpload;
